
import cdIcon from "./../../icon/src/icon.vue";
import selectBox from "./selectBox.vue";
import focus from "./focus";
import { defineComponent, ref, watch } from "vue";
const __default__ = defineComponent({
  name: "cd-cascader",
  components: {
    cdIcon,
    selectBox,
  },
  emits: ["update:modelValue", "onChange"],
  directives: {
    focus,
  },
  props: {
    placeholder: {
      type: String,
      default: "",
    },
    size: {
      type: Number,
      default: 200,
    },
    option: {
      type: Array,
      required: true,
    },
    showAllLevels: {
      type: Boolean,
      default: true,
    },
  },
  setup(props, context) {
    let sizeData = ref();
    if (props.size <= 150) {
      sizeData.value = 150;
    } else {
      sizeData.value = props.size;
    }
    let info = ref();
    let isFocus = ref();
    //isFocus
    function setIsFocus() {
      if (typeof isFocus.value == "undefined") {
        isFocus.value = true;
      } else if (isFocus.value == true) {
        isFocus.value = false;
      } else {
        isFocus.value = true;
      }
    }
    // 
    function getCascaderData(data: any) {
      if (props.showAllLevels == true) {
        info.value.value = data;
        setInputValue(data);
      } else {
        info.value.value = data.split("/")[data.split("/").length - 1];
        setInputValue(data.split("/")[data.split("/").length - 1]);
      }
    }
    window.addEventListener("click", () => {
      if (isFocus.value == true) {
        isFocus.value = false;
      }
    });
    function setInputValue(data: any) {
      context.emit("onChange", data);
      context.emit("update:modelValue", data);
    }
    return {
      isFocus,
      setIsFocus,
      getCascaderData,
      info,
      sizeData,
    };
  },
});

